.navbar_brand {
    height: fit-content;
    border: none;
    background-color: transparent;
}

.navbar_brand:focus{
    outline: none;
}

.navbar-brand > img {
    display: flex;
    justify-content: center;
    align-items: center;
    height: auto;
    width: 200px;
     max-height: 100%;
    max-width: 200px;
   margin: 0 auto;
}

@media only screen and (max-width: 425px) {
    .navbar-brand > img {
        width: 200px;
    }
}

@media only screen and (max-width: 375px) {
    .navbar-brand > img {
        width: 150px;
    }
}
